"use client"

import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table"
import { Card } from "@/components/ui/card"
import { Checkbox } from "@/components/ui/checkbox"
import { Input } from "@/components/ui/input"
import { useState } from "react"

interface Member {
  name: string
  accumulatedShares: string
  generatedProfit: number
  capitalize: boolean
  profitToCapitalize: number
  capitalizedShares: string
  additionalAmount: string
  withdrawalAmount: number
}

export default function SimplifiedDistribution() {
  const [members, setMembers] = useState<Member[]>(initialMembers)

  const handleCapitalize = (index: number, checked: boolean) => {
    setMembers(prevMembers => {
      const newMembers = [...prevMembers]
      const member = { ...newMembers[index] }
      member.capitalize = checked
      
      if (checked) {
        // When checked, set profitToCapitalize to generatedProfit and withdrawalAmount to 0
        member.profitToCapitalize = member.generatedProfit
        member.withdrawalAmount = 0
      } else {
        // When unchecked, set withdrawalAmount to generatedProfit and profitToCapitalize to 0
        member.withdrawalAmount = member.generatedProfit
        member.profitToCapitalize = 0
      }
      
      newMembers[index] = member
      return newMembers
    })
  }

  const handleProfitToCapitalizeChange = (index: number, value: string) => {
    setMembers(prevMembers => {
      const newMembers = [...prevMembers]
      const member = { ...newMembers[index] }
      const numValue = parseFloat(value) || 0

      member.profitToCapitalize = numValue
      if (numValue > member.generatedProfit) {
        member.additionalAmount = (numValue - member.generatedProfit).toFixed(2)
        member.withdrawalAmount = 0
      } else {
        member.additionalAmount = "0.00"
        member.withdrawalAmount = member.generatedProfit - numValue
      }
      
      newMembers[index] = member
      return newMembers
    })
  }

  return (
    <div className="space-y-8">
      <h2 className="text-3xl font-bold text-center mb-8">
        Resumen de distribución de utilidades simplificada
      </h2>

      {/* Profit Calculation Section */}
      <div className="grid grid-cols-1 md:grid-cols-4 gap-4 mb-8">
        {/* Gross Profit */}
        <Card className="p-4 bg-[#90EE90] shadow-lg">
          <h3 className="font-bold mb-2 text-lg">UTILIDAD BRUTA</h3>
          <div className="border-t border-black/10 pt-2">
            <div className="flex justify-between items-center py-1.5">
              <span className="font-medium">Intereses</span>
              <span className="font-semibold">29904.20</span>
            </div>
            <div className="flex justify-between items-center py-1.5">
              <span className="font-medium">Otros</span>
              <span className="font-semibold">0.20</span>
            </div>
            <div className="flex justify-between items-center py-1.5 mt-2 border-t border-black/10">
              <span className="font-bold">U.B. DU ACTUAL</span>
              <span className="font-bold">29904.40</span>
            </div>
          </div>
        </Card>

        {/* Expenses */}
        <Card className="p-4 bg-[#90EE90] shadow-lg">
          <h3 className="font-bold mb-2 text-lg">GASTOS</h3>
          <div className="border-t border-black/10 pt-2">
            <div className="flex justify-between items-center py-1.5">
              <span className="font-medium">G. Adm. Acum.</span>
              <span className="font-semibold">0.00</span>
            </div>
            <div className="flex justify-between items-center py-1.5">
              <span className="font-medium">I. Pag. Acum.</span>
              <span className="font-semibold">0.00</span>
            </div>
            <div className="flex justify-between items-center py-1.5">
              <span className="font-medium">Otros Acum.</span>
              <span className="font-semibold">0.00</span>
            </div>
            <div className="flex justify-between items-center py-1.5 mt-2 border-t border-black/10">
              <span className="font-bold">GASTOS DU ACTUAL</span>
              <span className="font-bold">0.0</span>
            </div>
          </div>
        </Card>

        {/* Net Profit */}
        <Card className="p-4 bg-[#90EE90] shadow-lg">
          <h3 className="font-bold mb-2 text-lg">U. Neta</h3>
          <div className="border-t border-black/10 pt-2 flex justify-center items-center h-24">
            <span className="text-3xl font-bold">29904.40</span>
          </div>
        </Card>

        {/* Reserves */}
        <Card className="p-4 bg-[#90EE90] shadow-lg">
          <h3 className="font-bold mb-2 text-lg">RESERVAS</h3>
          <div className="border-t border-black/10 pt-2">
            <div className="flex justify-between items-center py-1.5">
              <span className="font-medium">F.Social 10%</span>
              <span className="font-semibold">2990.44</span>
            </div>
            <div className="flex justify-between items-center py-1.5">
              <span className="font-medium">R.Legal 10%</span>
              <span className="font-semibold">2990.44</span>
            </div>
            <div className="flex justify-between items-center py-1.5 mt-2 border-t border-black/10">
              <span className="font-bold">TOTAL</span>
              <span className="font-bold">5980.88</span>
            </div>
          </div>
        </Card>
      </div>

      {/* Members Table */}
      <div className="rounded-lg border shadow-lg overflow-hidden bg-white">
        <div className="overflow-x-auto">
          <Table>
            <TableHeader>
              <TableRow className="bg-gray-100 border-b">
                <TableHead className="text-center font-bold py-4 px-6 whitespace-nowrap w-16">N°</TableHead>
                <TableHead className="font-bold py-4 px-6 whitespace-nowrap">SOCIOS</TableHead>
                <TableHead className="text-center font-bold py-4 px-6 whitespace-nowrap">N acciones acumuladas</TableHead>
                <TableHead className="text-center font-bold py-4 px-6 whitespace-nowrap">Utilidad generada</TableHead>
                <TableHead className="text-center font-bold py-4 px-6 whitespace-nowrap">Desea capitalizar?</TableHead>
                <TableHead className="text-center font-bold py-4 px-6 whitespace-nowrap">Utilidad a capitalizar(S/)</TableHead>
                <TableHead className="text-center font-bold py-4 px-6 whitespace-nowrap">Acciones capitalizadas</TableHead>
                <TableHead className="text-center font-bold py-4 px-6 whitespace-nowrap">Monto adicional(S/)</TableHead>
                <TableHead className="text-center font-bold py-4 px-6 whitespace-nowrap">Monto a retirar(S/)</TableHead>
              </TableRow>
            </TableHeader>
            <TableBody>
              {members.map((member, index) => (
                <TableRow key={index} className="hover:bg-gray-50 border-b">
                  <TableCell className="text-center font-medium py-4 px-6">{index + 1}</TableCell>
                  <TableCell className="font-medium py-4 px-6">{member.name}</TableCell>
                  <TableCell className="text-center py-4 px-6">{member.accumulatedShares}</TableCell>
                  <TableCell className="text-center py-4 px-6">{member.generatedProfit.toFixed(2)}</TableCell>
                  <TableCell className="text-center py-4 px-6">
                    <div className="flex justify-center">
                      <Checkbox 
                        checked={member.capitalize} 
                        onCheckedChange={(checked) => handleCapitalize(index, checked as boolean)}
                        className="h-5 w-5"
                      />
                    </div>
                  </TableCell>
                  <TableCell className="text-center py-4 px-6">
                    {member.capitalize ? (
                      <div className="flex justify-center">
                        <Input
                          type="number"
                          value={member.profitToCapitalize}
                          onChange={(e) => handleProfitToCapitalizeChange(index, e.target.value)}
                          max={member.generatedProfit}
                          min={0}
                          step="0.01"
                          className="w-32 text-center"
                        />
                      </div>
                    ) : (
                      <span className="text-gray-500">0.00</span>
                    )}
                  </TableCell>
                  <TableCell className="text-center py-4 px-6">
                    {member.capitalize ? member.capitalizedShares : "0"}
                  </TableCell>
                  <TableCell className="text-center py-4 px-6">
                    {member.capitalize ? member.additionalAmount : "0.00"}
                  </TableCell>
                  <TableCell className="text-center py-4 px-6 font-medium">
                    {member.withdrawalAmount.toFixed(2)}
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </div>
      </div>
    </div>
  )
}

const initialMembers: Member[] = [
  { name: "IVAN HUAMAN RAMOS", accumulatedShares: "348", generatedProfit: 3500.00, capitalize: false, profitToCapitalize: 0, capitalizedShares: "20", additionalAmount: "500.00", withdrawalAmount: 3500.00 },
  { name: "ESNIEDER LARA ROGRIGUEZ", accumulatedShares: "467", generatedProfit: 4200.00, capitalize: false, profitToCapitalize: 0, capitalizedShares: "30", additionalAmount: "700.00", withdrawalAmount: 4200.00 },
  { name: "MARCOS CRUZADO LARA", accumulatedShares: "259", generatedProfit: 2800.00, capitalize: false, profitToCapitalize: 0, capitalizedShares: "0", additionalAmount: "0.00", withdrawalAmount: 2800.00 },
  { name: "WILDER DOMIDEL QUINTANA MALUQUISH", accumulatedShares: "467", generatedProfit: 4100.00, capitalize: false, profitToCapitalize: 0, capitalizedShares: "30", additionalAmount: "600.00", withdrawalAmount: 4100.00 },
  { name: "ELITA CRUZADO PÉREZ", accumulatedShares: "285", generatedProfit: 2900.00, capitalize: false, profitToCapitalize: 0, capitalizedShares: "0", additionalAmount: "0.00", withdrawalAmount: 2900.00 }
]
